/*---var---*/
:root {
  --basic-color: #ffffff;
  --primary-color: #e69500;
  --dark-color: #000000;
  --light-color: #f2f2f2;
  --gray-color: #808080;
  --danger-color: #dc3545;
  --success-color: #4a28a7;
}

/*---BASICS---*/

* {
  margin: 0;
  padding: 0;
}

html,
body {
  font-family: 'Assistant', sans-serif;
}
/*----HEADER----*/
.header-nav {
  width: 100%;
}
.top-black {
  width: 100%;
  background-color: var(--dark-color);
}

.navbar {
  display: flex;
  padding-top: 8px;
  padding-bottom: 8px;
  width: 100%;
  max-width: 1070px;
  margin: 0 auto;
  justify-content: space-between;
  font-size: 14px;
}

.topnav {
  display: flex;
  list-style: none;
  width: 320px;
  justify-content: space-between;
}

.topnav .nav-link {
  text-decoration: none;
  color: var(--basic-color);
}

.topnav .nav-link:hover {
  color: var(--primary-color);
}

.private .private-link {
  text-decoration: none;
  color: var(--basic-color);
}

.private .private-link:hover {
  color: var(--primary-color);
}

.dropdownLink {
  display: none;
}

.header-down {
  background-color: var(--basic-color);
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1070px;
  margin: 5px auto;
  height: 120px;
}

.top-logo-menu {
  display: flex;
  align-items: flex-end;
}

.logo-rexi {
  height: 100px;
  margin-left: 10px;
}

.search {
  display: flex;
  align-items: flex-end;
}

.menu-top {
  display: flex;
  list-style: none;
}

.menu-link {
  text-decoration: none;
  color: var(--gray-color);
  font-weight: bold;
  font-size: 18px;
  margin-left: 10px;
  margin-right: 10px;
}

.menu-link:hover {
  color: var(--primary-color);
}

.menu-line {
  color: var(--gray-color);
  font-weight: bold;
  font-size: 18px;
  margin-left: 10px;
  margin-right: 10px;
}

.search-box {
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid #e6e6e6;
  border-bottom-width: 2px;

  height: 25px;
}

.search-img {
  height: 18px;
  margin-right: 5px;
}

/*----First Section----*/

.first-section {
  width: 100%;
  background-color: var(--light-color);
  margin-top: 10px;
  margin-bottom: 50px;
  height: 400px;
}

.container-best-articles {
  width: 100%;
  max-width: 1070px;
  margin: 0 auto;
  display: flex;
  height: 400px;
}

.main-article {
  margin-left: 5px;
  position: relative;
}
.main-article-img {
  height: 400px;
  width: 600px;
  position: relative;
  object-fit: cover;
}

.main-article-img2 {
  height: 200px;
  width: 300px;
  position: relative;
  object-fit: cover;
}

.main-article-img3 {
  height: 197px;
  width: 300px;
  position: relative;
  object-fit: cover;
}

.main-article-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  z-index: 1;
  bottom: 0;
  height: 50px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.main-article-headline {
  color: var(--basic-color);
  font-weight: bold;
  font-size: 18px;
  padding-right: 10px;
}

.main-article-author {
  color: var(--basic-color);
  font-size: 14px;
  padding-right: 10px;
}

.top-articles {
  display: flex;
  flex-direction: column;
  margin-left: 5px;
}

.top-article-div1 {
  height: 200px;
  margin-bottom: 3px;
  position: relative;
}

.top-article-div2 {
  height: 197px;
  position: relative;
}

.sidenav {
  background-color: #e69500;
  height: 400px;
  width: 160px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.lightbulb {
  max-height: 50px;
}

.newspaper {
  max-height: 50px;
}

.pet-brush {
  max-height: 50px;
}

.sidenav-link {
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: var(--basic-color);
  font-size: 18px;
  font-weight: bold;
}

/*----professional----*/

.professional {
  width: 100%;
  background-color: var(--light-color);

  margin-bottom: 25px;
}

.container-professional {
  width: 100%;
  max-width: 1070px;
  margin: 0 auto;
  display: flex;
}

.search-professional {
  width: 250px;
  background-color: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-professional-center {
  display: flex;
  flex-direction: column;
  height: 250px;
  justify-content: space-around;
}

.search-professional p {
  font-size: 18px;
  color: var(--basic-color);
  font-weight: bold;
  margin: 0 auto;
}

.search-professional select {
  padding-right: 5px;
  width: 200px;
  height: 30px;
  border-color: var(--basic-color);
  font-size: 16px;
  color: var(--gray-color);
  box-shadow: 5px 5px #cf8600;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat, repeat;
  background-position: left 0.7em top 50%, 0 0;
  background-size: 1em auto, 100%;
}

.search-btn {
  background-color: var(--basic-color);
  width: 80px;
  height: 30px;
  border: none;
  font-size: 16px;
  font-weight: bold;
  color: var(--gray-color);
  margin: 0 auto;
  box-shadow: 5px 5px #cf8600;
  cursor: pointer;
}
.search-btn:hover {
  background-color: var(--light-color);
}

.best-professional {
  width: 820px;
  display: flex;
  justify-content: space-around;
}

.trainer {
  display: flex;
  width: 240px;
  margin: 5px 10px 5px 10px;
}

.icon-professional img {
  width: 20px;
  padding-left: 10px;
}

.wrap-professional {
  display: flex;
  flex-direction: column;
  width: 207px;
}

.professional-headline {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.professional-headline p {
  color: var(--gray-color);
  font-size: 16px;
  font-weight: bold;
  padding-left: 10px;
}

.professional-headline a {
  text-decoration: none;
  color: var(--primary-color);
  font-size: 14px;
  font-weight: bold;
  margin: auto 0;
}

.professional-links {
  display: flex;
  margin-top: 7px;
}

.professional-links-img img {
  border-radius: 50%;
  height: 35px;
  width: 35px;
  margin-left: 10px;
  object-fit: cover;
}

.professional-links-details a {
  text-decoration: none;
  color: var(--primary-color);
  font-size: 16px;
  font-weight: bold;
}

.professional-links-details p {
  color: var(--gray-color);
  font-size: 12px;
  font-weight: bold;
}

.professional-links-more {
  display: flex;
  justify-content: flex-end;
}

.professional-links-more a {
  text-decoration: none;
  color: var(--primary-color);
  font-size: 14px;
  font-weight: bold;
}

/*---last-article---*/
.last-article {
  width: 100%;
  background-color: var(--light-color);
  margin-bottom: 25px;
}

.container-last-article {
  max-width: 1070px;
  margin: 0 auto;
}

.last-article-hedline {
  max-width: 1070px;
  margin: 0 auto;
}

.last-article-hedline h2 {
  font-size: 18px;
  font-weight: bold;
  color: var(--gray-color);
}

.articals {
  display: flex;
  justify-content: space-between;
}

.the-article {
  width: 350px;
}

.the-article img {
  width: 350px;
  height: 240px;
  object-fit: cover;
}

.the-article h3 {
  color: var(--primary-color);
  font-size: 16px;
  font-weight: bold;
  margin-top: 5px;
  margin-bottom: 5px;
}

.the-article p {
  color: var(--gray-color);
  font-size: 14px;
  margin-bottom: 7px;
  text-align: justify;
}

.the-article-author {
  display: flex;
}

.the-article-author h4 {
  color: var(--gray-color);
  font-size: 12px;
  font-weight: bold;
  margin-left: 5px;
  margin-bottom: 5px;
}

.more-articles {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.article-wrap h3 {
  color: var(--primary-color);
  font-size: 16px;
  font-weight: bold;
}

.article-author {
  display: flex;
}

.article-author h4 {
  color: var(--gray-color);
  font-size: 12px;
  font-weight: bold;
  margin-left: 5px;
}

.more-articles-link {
  display: flex;
  justify-content: flex-end;
}

.more-articles-link a {
  text-decoration: none;
  color: var(--primary-color);
  font-size: 14px;
  font-weight: bold;
}

.rexi-facebook img {
  height: 100%;
  width: 340px;
  object-fit: cover;
}

/*---products---*/
.products-headline {
  max-width: 1070px;
  margin: 0 auto;
}

.products-headline h2 {
  font-size: 18px;
  font-weight: bold;
  color: var(--gray-color);
}

.last-products {
  width: 100%;
  background-color: var(--light-color);
  margin-bottom: 50px;
}

.container-last-products {
  max-width: 1070px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.products {
  display: flex;
  justify-content: space-between;
}

.product-row {
  margin-left: 5px;
  position: relative;
}

.wrap-product-top {
  margin-bottom: 5px;
  position: relative;
}

.wrap-product-top {
  height: 175px;
  width: 250px;
  position: relative;
}

.wrap-product-top img {
  height: 175px;
  width: 250px;
  position: relative;
  object-fit: cover;
}

.wrap-product-bottom {
  height: 175px;
  width: 250px;
  position: relative;
}
.wrap-product-bottom img {
  height: 175px;
  width: 250px;
  position: relative;
  object-fit: cover;
}

.product-dog-top {
  position: relative;
  margin-bottom: 5px;
}

.product-dog-bottom {
  position: relative;
}

.product-dog-all {
  position: relative;
  margin-left: 5px;
  background-color: var(--gray-color);
  height: 175px;
  width: 250px;
}

.product-dog-top img {
  height: 175px;
  width: 250px;
  object-fit: cover;
}

.product-dog-top img {
  height: 175px;
  width: 250px;
  position: relative;
  object-fit: cover;
}

.product-dog-bottom {
  height: 175px;
  width: 250px;
  background-color: green;
}
.product-dog-bottom img {
  height: 175px;
  width: 250px;
  position: relative;
}

.product-dog-title {
  position: absolute;
  z-index: 1;
  bottom: 0;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.product-dog-all-link {
  background-color: var(--gray-color);
  text-decoration: none;
  color: var(--basic-color);
  font-size: 16px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 175px;
  width: 250px;
}

.wrap-all-products a {
  text-decoration: none;
  color: var(--basic-color);
}

.product-dog-title h3 {
  color: var(--basic-color);
  font-size: 16px;
  font-weight: bold;
  padding-right: 3px;
}

.product-dog-title p {
  color: var(--basic-color);
  font-size: 14px;
  padding-right: 3px;
}

.product-dog-title-link {
  position: absolute;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  z-index: 1;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-dog-title-link h3 {
  color: var(--basic-color);
  size: 14px;
  font-weight: bold;
  padding-right: 3px;
}

.product-dog-all-img {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-dog-all-img img {
  width: 60px;
  object-fit: cover;
}

.newsletter {
  width: 305px;

  background-color: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-newsletter {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 250px;
  height: 280px;
}

.newsletter-text {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.newsletter-text h4 {
  color: var(--basic-color);
  font-size: 18px;
  font-weight: bold;
  padding: 5px;
}

.newsletter-text p {
  color: var(--basic-color);
  font-size: 18px;
}

.newsletter-mail {
  padding-right: 5px;
  height: 35px;
  border: none;
  font-size: 16px;
  box-shadow: 5px 5px #cf8600;
  color: var(--gray-color);
}

.container-newsletter button {
  background-color: var(--basic-color);
  height: 35px;
  width: 80px;
  border: none;
  font-size: 16px;
  font-weight: bold;
  color: var(--gray-color);
  box-shadow: 5px 5px #cf8600;
  cursor: pointer;
}

.container-newsletter button:hover {
  background-color: var(--light-color);
}

/*---footer---*/
.footer {
  width: 100%;
  background-color: var(--dark-color);
}

.container-footer {
  max-width: 1070px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.footer-section {
  display: flex;
  flex-direction: column;
  padding-top: 5px;
  padding-bottom: 30px;
}

.footer-link {
  color: var(--basic-color);
  font-size: 14px;
  text-decoration: none;
  padding: 3px;
}

.footer-link img {
  height: 20px;
}

.rexi-social {
  padding-top: 10px;
  display: flex;
}

@media (max-width: 768px) {
  ul.topnav li a.nav-link {
    display: none;
  }

  ul.topnav li.dropdownLink {
    display: block;
    position: absolute;
    top: 2px;
    right: 5px;
    color: var(--basic-color);
  }

  ul.topnav.responsive {
    flex-direction: column;
  }

  ul.topnav.responsive li a {
    display: block;
  }

  ul.topnav.responsive li {
    position: relative;
    top: 10px;
    right: 5px;
    padding-bottom: 5px;
  }

  ul.topnav.responsive li.dropdownLink {
    position: absolute;
    top: 2px;
    right: 5px;
  }

  ul.topnav li.dropdownLink a.dropdown-link {
    color: var(--basic-color);
    text-decoration: none;
  }
}
